import axios from 'axios';

const API_BASE = 'https://www.tiendatotem.com/api';

function makeRequest(contentType, data, methodName, pathUrl, setResponse){
    
    axios(
        {
            url: `${API_BASE}/${pathUrl}`,
            method: methodName,
            data:data,
            headers:{
                'Content-Type': contentType
            }
        }
    ).then((response) => {
        setResponse(response.data.data, true)
    }).catch((error) => {
        console.log(error);
        setResponse(error,false)
    })
}

export{
    makeRequest , API_BASE
}