import React from 'react';
import Login from './Pages/Login';
import AdminPage from './Pages/AdminPage';
import ClientPage from './Pages/ClientPage';
import Register from './Pages/Register';
import UserPage from './Pages/UserPage';
import PageTotem from './components /PageTotem/MainPageTotem';

class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      auth : localStorage.getItem("login"),
      pageName : localStorage.getItem("pageName") === undefined ? "" : localStorage.getItem("pageName"),
      urlBringsParams: false,
      dataReady:false,
      addProductCarrito:false,
      idProductQR:0,
      idTotemQR:0
    }

    this.changeRol = this.changeRol.bind(this);
  }

  componentDidMount(){

    var sPaginaURL = window.location.search.substring(1);
    var sURLVariables = sPaginaURL.split('&');
    if(sURLVariables.length === 2){
      
      const arrP = sURLVariables[0].split('=');
      const arrT = sURLVariables[1].split('=');

      if(arrP[0] === "productId" && arrT[0] === "totemId"){
        console.log("se setean a true");
        localStorage.setItem('isNormalUser',true);
        localStorage.setItem('productId',arrP[1]);
        localStorage.setItem('totemId',arrT[1]);
        this.setState({ urlBringsParams : true,dataReady:true});
      }else{
        this.setState({ dataReady:true});
      }
    }else{
      localStorage.setItem('isNormalUser',false);
      this.setState({dataReady:true});
    }
  }

  changeRol(nameRol){
    this.setState( { pageName: nameRol } );
  }

  render(){
    const s = this.state

    return (
      <> 
        {
          s.dataReady &&
          s.urlBringsParams === false ?
          (() => {
              switch(s.pageName){
                case "Root" :
                  return <AdminPage changeRol={this.changeRol}/>
                case "Admin Tienda":
                  return <ClientPage changeRol={this.changeRol}/>
                case "registro":
                      return <Register changeRol={this.changeRol}/>
                case "Totem User":
                        return <PageTotem changeRol={this.changeRol}/>
                default:
                  return <Login changeRol={this.changeRol}/>
              }
            }
          )()
          :
          <UserPage/>
        }
      </>
    );

  }
  
}

export default App;
