import React,{Component} from 'react';

import Register from './Register';
import DetailProduct from '../components /PageUser/DetailProduct';
import Payment from '../components /PageUser/Payment';
import Carrito from '../components /PageUser/Carrito';
import ScanQR from '../components /PageUser/ScanQr';

class UserPage extends Component{

  state = {
    pageName:"",
    addProduct:true
  }

  changePageName = (page) => {
    this.setState({pageName:page,addProduct:false})
  }

  render(){
    const s = this.props;
    const state = this.state;

    return(
      <div className="container-fluid full-screen black d-flex flex-column p-0">
        <div className="row justify-content-center no-gutters title-store-totem">
          <div className="col-auto">
              <h3 className="title-mct">Totem Store</h3>
          </div>
        </div>
        {
          (() => {
              switch(this.state.pageName){
                case "Register":
                  return <Register changePage={this.changePageName}/>
                case "Pay":
                  return <Payment changePage={this.changePageName}/>
                case "Scan":
                  return <ScanQR changePage={this.changePageName}/>
                case "Detail":
                  return <DetailProduct changePage={this.changePageName} productId={s.productId} totemId={s.totemId}/>
                default:
                  return <Carrito changePage={this.changePageName} addProduct={state.addProduct}/>
              }
            }
          )()
        }
      </div>
    )
  }
}

export default UserPage;