import React from 'react';
import axios from 'axios';
import Toolbar from '../components /Toolbar/Toolbar';
import './Css/Register.css';

class Login extends React.Component{

    state={
        name:"",
        surName:"",
        email:"",
        userName:"",
        pass:"",
        phone:""
    }

    setName = (e) => {
        this.setState({ name : e.target.value})
    }

    setSurName = (e) => {
        this.setState({ surName : e.target.value})
    }

    setEmail = (e) => {
        this.setState({ email : e.target.value})
    }

    setUserName = (e) => {
        this.setState({ userName : e.target.value})
    }

    setPass = (e) => {
        this.setState({ pass: e.target.value })
    }

    setPhone = (e) => {
        this.setState({ phone: e.target.value })
    }

    onSubmit = (e) => {
        e.preventDefault();
        const s = this.state;
        const jsonSignUp = {
            name: s.name, 
            surName: s.surName,
            rolId: 4,
            email: s.email,
            userName: s.userName,
            passWord: s.pass,
            phone: s.phone,
            userCaptureId: 0,
            userUpdateId: 0,
            active: true,
            registerDate: "2019-11-25T22:51:20.650Z",
            updateDate: "2019-11-25T22:51:20.650Z"
        };

        axios.post('https://www.tiendatotem.com/api/User ', jsonSignUp)
            .then((res) => {
                console.log(res)
            }).catch((error) => {
                console.log(error)
            });
    }

    render(){
        return(
            <div className="container-login">
                <Toolbar drawerClickHandler={null} withDrawer={false}/> 
                <main className="main-login">
                    <form className="form-body">
                        <p className="spacer text-title">Bienvenido a Totem Store</p>
                        <p className="spacer text-title">Por favor ingrese sus datos para crear su cuenta</p>
                        <input className="spacer field-login" type="text" placeholder="Nombre(s)" onChange={this.setName}/>
                        <input className="spacer field-login" type="text" placeholder="Apellidos" onChange={this.setSurName}/>
                        <input className="spacer field-login" type="email" placeholder="Correo electrnico" onChange={this.setEmail}/>
                        <input className="spacer field-login" type="text" placeholder="Nombre de usuario" onChange={this.setUserName}/>
                        <input className="spacer field-login" type="password" placeholder="Contrasea" onChange={this.setPass}/>
                        <input className="btn-login spacer" onClick={this.onSubmit} type="submit" value="Registrarse" />
                    </form>
                </main>
            </div>
        );
    }
}

export default Login;